import React, { Fragment } from "react";
import GeneralPage from "./other/GeneralPage";
import data from "../data/learning/data.js";
import { Route, Switch, useRouteMatch, Link } from "react-router-dom";
import SinglePage from "./other/SinglePage";
import {
  Grid,
  Paper,
  makeStyles,
  Card,
  CardActionArea,
  CardMedia,
  CardContent,
  Typography,
  Button,
  CardActions,
  Container,
} from "@material-ui/core";
import Send from "@material-ui/icons/SendRounded";
import Dialpad from "@material-ui/icons/DialpadRounded";

import Helmet from "react-helmet";
import { React as profielImg } from "./../assets/img/Img";

const pageTitle = "آموزش‌ها";
const firstData = data.map((data) => {
  return (
    <GeneralPage
      key={data.id}
      title={data.title}
      url={data.url}
      id={data.id}
      img={data.img}
      desc={data.desc}
      summery={data.summery}
      date={data.date}
    />
  );
});

const useStyles = makeStyles((theme) => ({
  root: {
    padding: "12px",
  },
  fixedGrid: {
    position: 'sticky',
    top: "11%",
    bottom: 20, 
    zIndex: 5,
  },
  media: {
    height: 300,
  },
  dirRtl: {
    direction: "rtl",
  },
  route180: {
    transform: "rotate(180deg)",
  },
  mr10: {
    marginRight: "10px",
  },
  paper2:{
    top: "66%"
  }
}));

const LearningPage = () => {
  const classes = useStyles();
  let match = useRouteMatch();
  return (
    <Fragment>
      <Helmet>
        <title>{pageTitle}</title>
      </Helmet>
      <Switch>
        <Route path={match.path + "/:post"}>
          <SinglePage pageTitle={pageTitle} data={data} />
        </Route>
        <Route path={match.path}>
          <div className={classes.root}>
            <Grid justify="center" container spacing={1}>
              <Grid item xl={3} lg={3} md={3} sm={3} xs={3}>
                <Paper className={classes.fixedGrid} elevation={2}>
                  <Card className={classes.root}>
                    <CardActionArea>
                      <CardMedia
                        className={classes.media}
                        image={profielImg}
                        title="Contemplative Reptile"
                      />
                      <CardContent className={classes.dirRtl}>
                        <Typography gutterBottom variant="h5" component="h2">
                          آموزش برای شماست
                        </Typography>
                        <Typography
                          variant="body2"
                          color="textSecondary"
                          component="p"
                        >
                          یه توضیح مختصری اینجا میادش که بعد ها کلا این قسمت
                          قراره بشه بیشترین برچسب‌ها و بازدید و این چیزا دیگه.
                        </Typography>
                      </CardContent>
                    </CardActionArea>
                    <CardActions className={classes.dirRtl}>
                      <Link to="/send-course">
                        <Button size="small" color="primary">
                          درخواست آموزش
                          <Send
                            fontSize="small"
                            className={classes.route180 + " " + classes.mr10}
                          />
                        </Button>
                      </Link>
                      <Button size="small" color="primary">
                        تماس مستقیم با من
                        <Dialpad fontSize="small" className={classes.mr10} />
                      </Button>
                    </CardActions>
                  </Card>
                </Paper>
                <Paper className={classes.fixedGrid + " " + classes.paper2} elevation={2}>
                  <Card className={classes.root}>
                    <CardActionArea>
                      <CardContent className={classes.dirRtl}>
                        <Typography align="center" gutterBottom variant="h3" component="h2">
                          رایگان
                        </Typography>
                      </CardContent>
                    </CardActionArea>
                  </Card>
                </Paper>
              </Grid>
              <Grid item xl={9} lg={9} md={9} sm={9} xs={9}>
                <Container fixed>{firstData}</Container>
              </Grid>
            </Grid>
          </div>
        </Route>
      </Switch>
    </Fragment>
  );
};

export default LearningPage;
