import React, { Fragment } from "react";
import { useParams } from "react-router-dom";
import Helmet from "react-helmet";
import {
  Grid,
  Container,
  Paper,
  Card,
  Button,
  makeStyles,
  CardActionArea,
  CardMedia,
  CardContent,
  Typography,
  CardActions,
} from "@material-ui/core";
import Share from "@material-ui/icons/ShareRounded";
import LoadPost from "./LoadPost";

const useStyles = makeStyles((theme) => ({
  root: {
    padding: "12px",
  },
  fixedGrid: {
    position: "sticky",
    top: "11%",
    bottom: 20,
    zIndex: 5,
  },
  media: {
    height: 220,
  },
  dirRtl: {
    direction: "rtl",
  },
  route180: {
    transform: "rotate(180deg)",
  },
  mr10: {
    marginRight: "10px",
  },
  title: {
    textAlign: "center",
    direction: "rtl",
    paddingTop: "20px",
    paddingBottom: "20px",
    color: "#1565C0",
  },
  postMedia:{
    padding: 100,
    paddingTop: 0,
    paddingBottom: 0
  }
}));

const SinglePage = (props) => {
  const classes = useStyles();
  let { post } = useParams();
  let notFound = false;
  const firstData = props.data.map((data) => {
    if (data.url === "/" + post) {
      notFound = true;
      return (
        <Fragment key={data.id} >
          <Helmet>
            <title>{data.title + " - " + props.pageTitle}</title>
          </Helmet>
          <div className={classes.root}>
            <Grid justify="center" container spacing={1}>
              <Grid item xl={3} lg={3} md={3} sm={3} xs={3}>
                <Paper className={classes.fixedGrid} elevation={2}>
                  <Card className={classes.root}>
                    <CardActionArea>
                      <CardContent className={classes.dirRtl}>
                        <Typography gutterBottom variant="h5" component="h2">
                          {data.title}
                        </Typography>
                        <CardMedia
                          className={classes.media}
                          image={data.img}
                          title="Contemplative Reptile"
                        />
                        <Typography
                          variant="body2"
                          color="textSecondary"
                          component="p"
                        >
                          {data.summery}
                        </Typography>
                      </CardContent>
                    </CardActionArea>
                    <CardActions className={classes.dirRtl}>
                      <Button size="small" color="primary">
                        اشتراک گذاری
                        <Share
                          fontSize="small"
                          className={classes.route180 + " " + classes.mr10}
                        />
                      </Button>
                    </CardActions>
                  </Card>
                </Paper>
              </Grid>
              <Grid item xl={9} lg={9} md={9} sm={9} xs={9}>
                <Container fixed>
                  <Card className={classes.root}>
                    <Typography
                      className={classes.title}
                      gutterBottom
                      variant="h4"
                      component="h1"
                    >
                      {data.title}
                    </Typography>
                    <CardMedia
                      component="img"
                      alt={data.title}
                      height="100%"
                      image={data.img2}
                      title={data.title}
                      className={classes.postMedia}
                    />
                    <CardContent>
                      <Typography
                        variant="body2"
                        color="textSecondary"
                        component="div"
                        id="loadPosts"
                      >
                        <LoadPost dataDesc = {data.desc} />
                      </Typography>
                    </CardContent>
                    <CardActions>
                      <Button size="small" color="primary">
                        Share
                      </Button>
                      <Button size="small" color="primary">
                        Learn More
                      </Button>
                    </CardActions>
                  </Card>
                </Container>
              </Grid>
            </Grid>
          </div>
        </Fragment>
      );
    }
    return null;
  });
  return <Fragment>{notFound ? firstData : <h1>Not Found</h1>}</Fragment>;
};

export default SinglePage;
